@import "../../common.scss";

.icon {
  // 기존 공통 스타일
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.3s;

  .title {
    font-size: 1.5rem;
    font-weight: 600;
  }

  &.profile_wrap,
  &.skills_wrap,
  &.info_wrap {
    color: $neonblue2;
    transition: 0.2s ease;

    &:hover {
      color: $neonblue1;
    }
  }
  &.active {
    color: $neonblue1;
  }
}
