@import "../../common.scss";

.profileSection {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
}

.profileContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem;
}
.profileText_wrap {
  color: white;
  p,
  h3 {
    opacity: 0;
    transform-origin: 0% 50% -50;
    animation: fadeInUp 0.8s ease-in-out forwards;
    animation-delay: var(--animation-delay);
  }
  h3 {
    font-size: 1.5rem;
  }
  p {
    font-size: 1rem;
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(80px) rotateX(180deg);
  }
  100% {
    opacity: 1;
    transform: translateY(0) rotateX(0);
  }
}
